import { todosSlice } from "../redux/reducers/todo";
import { AppDispatch } from "../redux/store";
import storageService from "./storageService";
import { produce } from "immer";
import { findIndex } from "lodash";

export const getAllTodos = () => (dispatch: AppDispatch) => {
  const lsTodos = storageService.get<TodoItem[]>("todo");
  lsTodos ? dispatch(todosSlice.actions.getAll(lsTodos)) : dispatch(todosSlice.actions.getAll([]));
};

export const addTodo = (item: TodoItem) => (dispatch: AppDispatch) => {
  const lsTodos = storageService.get<TodoItem[]>("todo");
  if (lsTodos) {
    storageService.set("todo", [item, ...lsTodos]);
    dispatch(todosSlice.actions.getAll([item, ...lsTodos]));
  } else {
    storageService.set("todo", [item]);
    dispatch(todosSlice.actions.getAll([item]));
  }
};

export const completeTodo = (id: string, list: TodoSlice) => (dispatch: AppDispatch) => {
  const findCompleteTodo = list.all.find((item) => item.id === id);
  const findInd = findIndex(list.all, { id });

  if (!!findCompleteTodo && findCompleteTodo.completed) {
    const updatedAll = produce(list, (item: TodoSlice) => {
      item.all[findInd].completed = false;
    });

    dispatch(todosSlice.actions.getAll(updatedAll.all));
    storageService.set("todo", updatedAll.all);
    return;
  }

  const updatedAll = produce(list, (item: TodoSlice) => {
    item.all[findInd].completed = true;
  });

  dispatch(todosSlice.actions.getAll(updatedAll.all));
  storageService.set("todo", updatedAll.all);
};

export const removeTodo = (id: string, list: TodoSlice) => (dispatch: AppDispatch) => {
  const filteredList = list.all.filter((item) => item.id !== id);
  const completed = filteredList.filter((item) => item.completed);
  storageService.set("todo", filteredList);
  dispatch(todosSlice.actions.getAll(filteredList));

  if (
    (list.filter === "completed" && !completed.length) ||
    (list.filter === "inProgress" && !!completed.length)
  ) {
    dispatch(todosSlice.actions.setFilter("all"));
  }
};

export const removeAll = () => (dispatch: AppDispatch) => {
  storageService.set("todo", []);
  dispatch(todosSlice.actions.getAll([]));
};

export const filterTodos = (type: TTodoFilter) => (dispatch: AppDispatch) => {
  dispatch(todosSlice.actions.setFilter(type));
};
