import React, { BaseSyntheticEvent, useState } from "react";
import { v4 } from "uuid";

import { Button } from "../../../components";

import { useAppDispatch, useAppSelector } from "../../../hooks/redux";
import { addTodo, filterTodos, removeAll } from "../../../service/todosService";

import classes from "./style.module.scss";

const ToDoForm: React.FC = () => {
  const dispatch = useAppDispatch();
  const { todos } = useAppSelector((state) => state);
  const [todo, setTodo] = useState({
    id: "",
    task: "",
    completed: false,
  });

  const completed = todos.all.filter((item) => item.completed).length;
  const inProgress = todos.all.filter((item) => !item.completed).length;

  const handleChange = (e: BaseSyntheticEvent) => {
    setTodo({ ...todo, task: e.target.value });
  };

  const handleSubmit = (e: BaseSyntheticEvent) => {
    e.preventDefault();
    if (todo.task.trim()) {
      dispatch(addTodo({ ...todo, id: v4() }));
      setTodo({ ...todo, task: "" });
    }
  };

  const removeHandler = () => {
    dispatch(removeAll());
  };

  const filterHandler = (e: BaseSyntheticEvent) => {
    dispatch(filterTodos(e.currentTarget.id));
  };

  return (
    <>
      <form onSubmit={handleSubmit} className={classes.root}>
        <input
          placeholder="I must to do!!!"
          className={classes.todoInput}
          value={todo.task}
          onChange={handleChange}
          maxLength={40}
        />
        <span className={classes.addBtn}>
          <Button.Custom className={classes.todoBtn} text="To Do" type="submit" />
        </span>
      </form>
      <Button.Custom className={classes.todoBtn} onClick={removeHandler} text="Clear all" />
      <div className={classes.btnGroup}>
        <Button.Custom
          id="all"
          className={classes.todoBtn}
          onClick={filterHandler}
          text={`All ${todos.all.length ? `(${todos.all.length})` : ""}`}
        />
        <Button.Custom
          id="completed"
          className={classes.todoBtn}
          onClick={filterHandler}
          text={`Completed ${completed ? `(${completed})` : ""}`}
        />
        <Button.Custom
          id="inProgress"
          className={classes.todoBtn}
          onClick={filterHandler}
          text={`In progress ${inProgress ? `(${inProgress})` : ""}`}
        />
      </div>
    </>
  );
};

export default ToDoForm;
