import React from "react";
import { Checkbox, IconButton, Typography } from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";

import { useAppDispatch, useAppSelector } from "../../hooks/redux";
import { completeTodo, removeTodo } from "../../service/todosService";

import classes from "./style.module.scss";

interface IProps {
  todo: TodoItem;
}

export const Todo: React.FC<IProps> = ({ todo }) => {
  const dispatch = useAppDispatch();
  const todoList = useAppSelector((state) => state.todos);

  const checkboxClick = () => {
    dispatch(completeTodo(todo.id, todoList));
  };

  const removeClick = () => {
    dispatch(removeTodo(todo.id, todoList));
  };

  return (
    <div className={classes.root}>
      <li className={classes.row}>
        <Checkbox checked={todo.completed} onClick={checkboxClick} />
        <Typography>{todo.task}</Typography>
      </li>
      <IconButton
        style={{ width: "55px", height: "55px" }}
        onClick={removeClick}
        disableRipple
        disableFocusRipple
        disableTouchRipple
      >
        <DeleteIcon />
      </IconButton>
    </div>
  );
};
