import React, { BaseSyntheticEvent } from "react";

interface IProps {
  className?: string;
  text?: string;
  onClick?: (e: BaseSyntheticEvent) => void;
  value?: string;
  type?: "button" | "reset" | "submit" | undefined;
  id?: string;
}
const CustomButton: React.FC<IProps> = ({ className, text, onClick, value, type, id }) => {
  return (
    <button id={id} className={className} value={value || text} type={type} onClick={onClick}>
      {text}
    </button>
  );
};

export default CustomButton;
